<template>
  <div id="app">
    <header>
      <div class="header__body text-center container-fluid">
        <div class="row p-4">
          <div class="header__heading offset-sm-5 col-2 lead">
            CREDITON
          </div>
          <div class="header__lang offset-sm-2 offset-7 col-3 col-sm-2 col-xl-1">
            <span class="header__lang__ru small">РУ </span>
            <span class="header__lang__ukr small text-white">| УКР</span>
          </div>
        </div>
      </div>
    </header>
    <main>
      <h3 class="body__header pt-sm-5 text-center mt-sm-5 mt-1 mb-5 font-weight-normal">Добавление платежной карты</h3>
      <div class="form container border rounded">
        <div class="row mt-sm-5 mt-3">
          <div class="offset-lg-4 col-lg-4 offset-sm-3 col-sm-6 offset-1 col-10">
            <form action="" class="mt-1">
              <div class="row">
                <strong class="mb-3 form__title">Номер карты</strong>
                <input type="text" v-model="card.number" placeholder="Номер карты" class="form__input-primary col-12 py-3"
                :class="dangerNum"
                :style="'background-image: url('+ image +')'"> 
                <small class="danger" v-if="!check.number">*{{ message.number }}</small>
                <div class="d-flex mt-2">
                  <div style="margin-right: 1em;">
                    <input type="text"
                    @input="parseDate" 
                    v-model="card.date" 
                    class="form__input-secondary px-2 py-3" 
                    :class="dangerDate" 
                    placeholder="Срок действия">
                    <div class="danger small" v-show="!check.date" style="margin-right: 1em;">*{{ message.date }}</div>
                  </div>
                  <div>
                    <input type="password" 
                    v-model="card.CVC" 
                    class="form__input-secondary px-2 py-3"
                    :class="dangerCVC"
                    @input="parseCvc" 
                    placeholder="CVC код">
                    <div class="danger small" v-show="!check.CVC">*{{ message.CVC }}</div>
                  </div>
                </div>
                <p class="form__description mt-3">
                  Для привязки карты мы проведем платеж в размере 1.00 UAH, который будет возвращен в течение 30 минут 
                </p>
              </div>
              <div class="row">
                <button 
                class="form__btn col-12 btn text-white py-3 border-0 mb-sm-5 mb-3" 
                @click.prevent="formControl"
                v-if="!showLoader">
                  <span class="small font-weight-bold">Получить деньги</span>
                </button>
                <section v-else class="col-12">
                  <div class='sk-circle-bounce'>
                    <div class='sk-child sk-circle-1'></div>
                    <div class='sk-child sk-circle-2'></div>
                    <div class='sk-child sk-circle-3'></div>
                    <div class='sk-child sk-circle-4'></div>
                    <div class='sk-child sk-circle-5'></div>
                    <div class='sk-child sk-circle-6'></div>
                    <div class='sk-child sk-circle-7'></div>
                    <div class='sk-child sk-circle-8'></div>
                    <div class='sk-child sk-circle-9'></div>
                    <div class='sk-child sk-circle-10'></div>
                    <div class='sk-child sk-circle-11'></div>
                    <div class='sk-child sk-circle-12'></div>
                  </div>
              </section>
              </div>
            </form>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import luhn from 'fast-luhn'
export default {
  name: 'App',
  data() {
    return {
      card: {
        number: '',
        date: '',
        CVC: ''
      },
      check: {
        number: true,
        date: true,
        CVC: true
      },
      message: {
        number: 'Некорректные данные',
        date: 'Некорректные данные',
        CVC: 'Некорректные данные'
      },
      showLoader: false
    }
  },
  methods: {
    GetCardType(number) {
      // Visa
      let re = new RegExp("^4");
      if (number.match(re) != null) return "Visa";

      // Mastercard 
      // Updated for Mastercard 2017 BINs expansion
      if (/^(5[1-5][0-9]{14}|2(22[1-9][0-9]{12}|2[3-9][0-9]{13}|[3-6][0-9]{14}|7[0-1][0-9]{13}|720[0-9]{12}))$/.test(number)) return "Mastercard";

    },
    loader() {
      this.showLoader = true
      let b = this
      setTimeout(function() {
        b.showLoader = false
        alert('Good')
      }, 2000)
    },
    parseCvc(e) {
      if(!parseInt(e.data)&&e.data!==null&&e.data!=='0') {
        let word = this.card.CVC.toString()
        word = word.split('')
        word.pop()
        word = word.join('')
        this.card.CVC = word
      }
    },
    parseDate(e) {
      if(!parseInt(e.data)&&e.data!==null) {
        let word = this.card.date.toString()
        word = word.split('')
        word.pop()
        word = word.join('')
        this.card.date = word
      }
      if(e.target.value.length > 2) {
        let number = e.target.value.replace('/', '')
        let word = number.slice(0, 2) + '/' + number.slice(2, 4)
        this.card.date = word
      }
    },
    checkInput() {
      const num = this.number.split(' ').join('')
      const el = 'n'
      if(num.length === 0) {
        this.msgOutput(el, false, 'Обязательное поле')
      } else if(num.length !== 16) {
        this.msgOutput(el, false, 'Некорректные данные')
      } else if(!luhn(num)) {
        this.msgOutput(el, false, 'Некорректные данные')
      } else {
        this.msgOutput(el, true)
      }
    },
    checkDate() {
      let date = this.expDate
      if(date.length === 0) {
        this.msgOutput('d', false, 'Обязательное поле')
      } else if(date.length < 5) {
        this.msgOutput('d', false, 'Некорректные данные')
      } else {
        let month = date.slice(0, 2)
        let year = date.slice(3, 5)
        if(month > 12||year < 20||year > 30) {
          this.msgOutput('d', false, 'Некорректные данные')
        } else this.msgOutput('d', true)
      }
    },
    checkCVC() {
      let cvc = this.card.CVC
      if(cvc.length === 0) {
        this.msgOutput('x', false, 'Обязательное поле')
      } else if(cvc.length < 3) {
        this.msgOutput('x', false, 'Некорректные данные')
      } else {
        this.msgOutput('x', true)
      }
    },
    formControl() {
      this.checkInput()
      this.checkDate()
      this.checkCVC()
      this.checkAll()
    },
    checkAll() {
      if(this.check.date === true&&this.check.number === true&&this.check.CVC === true) {
        this.loader()
      }
    },
    msgOutput(el, val, msg = '') {
      if(el === 'n') {
        this.check.number = val
        this.message.number = msg
      } else if (el === 'x') {
        this.check.CVC = val
        this.message.CVC = msg
      } else if (el === 'd') {
        this.check.date = val
        this.message.date = msg
      } 
    }
  },
  computed: {
    dangerDate() {
      return this.check.date ? '' : 'danger-border'
    },
    dangerNum() {
      return this.check.number ? '' : 'danger-border'
    },
    dangerCVC() {
      return this.check.CVC ? '' : 'danger-border'
    },
    number() {
      return this.card.number
    },
    expDate() {
      return this.card.date
    },
    CVC() {
      return this.card.CVC
    },
    image() {
      let arr = this.card.number.split(' ')
      let type = 0
      type = this.GetCardType(arr.join(''))
      if(type === 'Visa') return require('./images/Visa.png')
      else if (type === 'Mastercard') return require('./images/Mastercard.png')
      return ''
    }
  },
  watch: {
    CVC(num) {
      if(num.length > 3) {
        num = num.slice(0, 3)
        this.card.CVC = num
      }      
    },
    expDate(num) {
      if(num.length > 5) {
        num = num.slice(0, 5)
        this.card.date = num
      }
      if(num.length === 3) {
        if(num.slice(-1) === '/') num = num.slice(0, -1)
        this.card.date = num        
      }
    },
    number(num) {
      num = num.replace(/[^\dA-Z]/g , '').replace(/(.{4})/g, '$1 ').trim()
      const arr = num.split(' ')
      if(arr.length === 5) arr.pop()
      num = arr.join(' ')
      this.card.number = num
    }
  }
}
</script>

